import React, { Suspense, useState, useEffect } from 'react';
import { Canvas } from '@react-three/fiber';
import { OrbitControls, useGLTF } from '@react-three/drei';
import { MeshStandardMaterial } from 'three';
import './App.css';
import {CircleLoader} from "react-spinners"; 

function DressModel({ onLoad }) {
  const { scene } = useGLTF('/wedding_dress_obj/scene.gltf');


  useEffect(() => {
    if (scene) {
      scene.traverse((child) => {
        if (child.isMesh) {
          child.material = new MeshStandardMaterial({
            color: 0xffffff,
            roughness: 0.1,
            metalness: 0.2,
          });
        }
      });
      if (onLoad && typeof onLoad === 'function') {
        onLoad();
      }
    }
  }, [scene, onLoad]);

  return <primitive object={scene} scale={1.8} position={[0, -2, 0]} />;
}

function Homepage() {
  const [isLoaded, setIsLoaded] = useState(false);
  const [animateButton, setAnimateButton] = useState(false);

  const handleLoad = () => setIsLoaded(true);

  const scrollToRecommendation = () => {
    window.scrollTo({
      top: 2615,
      behavior: "smooth"
    });
    setAnimateButton(true);
    setAnimateButton(!animateButton);
  };

  return (
    <div id="dress-container" style={{ position: 'relative', width: '100vw', height: '100vh', overflow: 'hidden' }}>
      {!isLoaded && (
        <span style={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', zIndex: 10 }}>
           ...
        </span>
      )}
      <Canvas
        style={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%' }}
        camera={{ position: [0, 1, 5] }}
      >
        <Suspense fallback={null}>
          <OrbitControls
            autoRotate={true}
            enableZoom={false}
            maxPolarAngle={Math.PI / 1.5}
            minPolarAngle={Math.PI / 3}
            target={[0, -0.5, 0]}
          />
          <ambientLight intensity={1} />
          <directionalLight position={[0, 10, 0]} intensity={2} />
          <directionalLight position={[-10, 10, 0]} intensity={2} />
          <directionalLight position={[10, 10, 0]} intensity={2} />
          <DressModel onLoad={handleLoad} />
        </Suspense>
      </Canvas>

      {/*    */}
      <div className="dress-text" style={{fontSize:"50px", position: 'absolute', top: '10%', left: '10%', zIndex: 10 }}>
        <h1 style={{ fontSize: '2em', color: 'white' }}>The Best</h1>
      </div>

      {/*    */}
      <div className="dress-text" style={{ fontSize:"30px", position: 'absolute', bottom: '35%', right: '7%', zIndex: 10 }}>
        <h1 style={{ fontSize: '2em', color: 'white' }}>AI Dress Stylist</h1>
      </div>
      <div className="dress-text" style={{ fontSize:"20px", position: 'absolute', bottom: '25%', right: '7%', zIndex: 10 }}>
        <h1 style={{ fontSize: '2em', color: 'white' }}>For You</h1>
      </div>

      <div
        className="dress-text"
        style={{
          position: 'absolute',
          top: '85%',
          width: '100%',
          textAlign: 'center',
          zIndex: 10,
          fontSize: '2em',
        }}
      >
        <div className="dress-flexbox">
          <span
            className={`startBtn`}
       
          >
               
          </span>
          <CircleLoader
                color="rgba(12, 80, 164, 1)"
                className={`startBtn ${animateButton ? 'animate-color' : ''}`}
                onClick={scrollToRecommendation}
            />
          <span
            className={`startBtn `}
           style={{marginLeft:"20px"}}
          >
             
          </span>
   
        </div>
      </div>

      {/*     */}
      <div className="soft-snowflakes" aria-hidden="true">
        {Array.from({ length: 30 }).map((_, index) => (
          <div key={index} className="soft-snowflake">
            
          </div>
        ))}
      </div>
    </div>
  );
}

export default Homepage;
