import "./App.css";
import NavigationBar from "./NavigationBar";
import ControlCarousel from "./carousel";

import dressOptions from "./data";
import optionTranslations from "./optionsdata";

import { HashLoader } from "react-spinners"; // react-spinners HashLoader 
import React, { useRef } from "react";

import {
  BrowserRouter as Router,
  Routes,
  Route,
  useNavigate,
} from "react-router-dom";
import { useEffect, useState } from "react";

function Generator() {
  let [options, setOptions] = useState(dressOptions);
  let [Translations, setOptionTranslations] = useState(optionTranslations);
  let [step, setStep] = useState(0);
  let [nextBtn, SetNextBtn] = useState("");
  let [pointer, setPointer] = useState(0);
  let [selectedOptions, setSelectedOptions] = useState(
    Array(dressOptions.length).fill(null)
  );
  let [otherInput, setOtherInput] = useState("");
  let [activeSteps, setActiveSteps] = useState([
    true,
    ...new Array(dressOptions.length - 1).fill(false),
  ]);
  const [isGenerated, setIsGenerated] = useState(false);
  const [showOptions, setShowOptions] = useState(false);
  let navigate = useNavigate();
  const [scrollAnimation, setScrollAnimation] = useState(false);
  const [showAIButton, setShowAIButton] = useState(true);
  const [showGenerator, setShowGenerator] = useState(false);
  let [backgroundColor, setBackgroundColor] = useState(
    "linear-gradient(to right, #0a0f0d 0%, #002233 100%"
  ); //   
  let [isActive, setIsActive] = useState(false);
  const [loading, setLoading] = useState(false);

  const [animate, setAnimate] = useState(false);
  const [darkBackgroundZIndex, setDarkBackgroundZIndex] = useState(10);
  const [lightBackgroundZIndex, setLightBackgroundZIndex] = useState(0);

  const [imageNames, setImageNames] = useState(Array(6).fill("")); // 6    

  const [checkedImages, setCheckedImages] = useState(Array(6).fill(false));
  const [selectedImageSrcs, setSelectedImageSrcs] = useState([]);

  const [compositedImageSrcs, setCompositedImageSrcs] = useState([
    "",
    "",
    "",
    "",
    "",
    "",
  ]);

  const [image, setImage] = useState(null);
  const [buttonText, setButtonText] = useState(
    "    \n \n    \n , 1MB  "
  );

  const fileInput = useRef(null); // useRef input   
  const [buttonText2, setButtonText2] = useState(
    "   \n \n     ! "
  ); //    
  const [showResults, setShowResults] = useState(false); //   

  const [buttonBackground, setButtonBackground] = useState("#E5E5CB");

  const [mappingIndex, setMappingIndex] = useState([]);
  const [clickedImageIndices, setClickedImageIndices] = useState([]); // Changed to an array to store multiple indices

  const [imageSrc, setImageSrc] = useState("");

  //    
  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.scrollY;
      if (scrollPosition > 250) {
        setScrollAnimation(true);
      } else {
        setScrollAnimation(false);
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  useEffect(() => {
    if (isGenerated) {
      const timer = setTimeout(() => {
        setShowOptions(true);
        setAnimate(false); //      false 
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [isGenerated]);

  const scrollTo1800px = () => {
    window.scrollTo({
      top: 2200,
      behavior: "smooth",
    });
  };

  const renderTextWithBreaks = (text, highlightText) => {
    return text.split("\n").map((line, index) => (
      <span key={index}>
        {line.includes(highlightText) ? (
          <span style={{ color: "#3C2A21", fontWeight: "bold" }}>{line}</span>
        ) : (
          line
        )}
        {index < text.split("\n").length - 1 && <br />}
      </span>
    ));
  };

  // //  
  // const handleFormSubmit1 = async (event) => {
  //   setLoading(true); //  
  //   // Translate selected options using the mapping

  //   setIsActive(true); //   true   
  //       setTimeout(() => {
  //         setAnimate(true);
  //         setDarkBackgroundZIndex(0); // dark-background z-index 
  //         setLightBackgroundZIndex(10); // light-background z-index 
  //       }, 2000); // 2   

  //   setLoading(false); //     
  // };

  const handleFormSubmit = async (event) => {
    setLoading(true); //  
    // Translate selected options using the mapping
    const translatedOptions = selectedOptions.map((option) =>
      option ? Translations[option] || option : null
    );
    // FormData  
    const formData = new FormData();

    console.log(translatedOptions);
    //   JSON    formData 
    formData.append("options", JSON.stringify(translatedOptions));

    //     formData 
    formData.append("otherInput", otherInput);

    console.log(formData);
    try {
      const response = await fetch(
        "http://real.pinkbean.co.kr:1557/new_dress_test",
        {
          method: "POST",
          body: formData, // 'Content-Type': 'multipart/form-data'  .
        }
      );

      if (response.ok) {
        const jsonResponse = await response.json();

        const imageSrcArray = jsonResponse.results.map(
          (result) => `data:image/jpeg;base64,${result}`
        );

        setMappingIndex(jsonResponse.imageMapKeys);

        setImageNames(imageSrcArray);
        setIsActive(true); //   true   
        setTimeout(() => {
          setAnimate(true);
          setDarkBackgroundZIndex(0); // dark-background z-index 
          setLightBackgroundZIndex(10); // light-background z-index 
        }, 3000); // 2   
      } else {
        throw new Error("Failed to post data");
      }
    } catch (error) {
      console.error("Error during form submission:", error);
    }
    setLoading(false); //     
  };

  const handleImageChange = (event) => {
    const file = event.target.files;

    if (file) {
      setImage(file);
      console.log(file);
      setButtonText("  "); //      
      setButtonBackground("#D4BDAC");
      console.log(file);
    }
  };

  // const handleVirtualFittingStart11 = async (event) => {
  //   event.preventDefault(); //      
  //   setLoading(true); //  
  //   setIsActive(true); //   true   
  //   setTimeout(() => {

  //     setAnimate(true);
  //        setShowResults(true);
  //     setDarkBackgroundZIndex(0); // dark-background z-index 
  //     setLightBackgroundZIndex(10); // light-background z-index 
  //   }, 2000); // 2   

  //   setLoading(false); //  
  // };

  const handleVirtualFittingStart = async (event) => {
    event.preventDefault(); //      
    setLoading(true); //  

    // FormData  
    const formData = new FormData();
    console.log(clickedImageIndices);

    const extractedValues = clickedImageIndices.map(
      (index) => mappingIndex[index]
    );
    console.log(extractedValues);

    //   JSON    formData 

    //     formData 
    formData.append("ImageFileIndexArray", extractedValues);
    console.log(clickedImageIndices);
    formData.append("myPic", image[0]);

    console.log(image);
    console.log(formData);
    //   POST  (:     )
    try {
      const response = await fetch(
        "http://real.pinkbean.co.kr:1557/vton_dress_test",
        {
          method: "POST",
          body: formData,
        }
      );

      const jsonResponse = await response.json();
      if (response.ok) {
        //    

        setShowResults(true);
        setButtonText2(" "); //      
        const imageSrcArray = [];

        // Use a for loop to build the image URL array
        for (let i = 0; i < jsonResponse.results.length; i++) {
          const imageSrc = `data:image/jpeg;base64,${jsonResponse.results[i]}`;
          imageSrcArray.push(imageSrc);
        }

        // Check if there are elements in the array
        if (imageSrcArray.length > 0) {
          // Set the state for the last image source
          setImageSrc(imageSrcArray[imageSrcArray.length - 1]);

          // Remove the last element for the composited images state
          imageSrcArray.pop(); // This modifies the original array and removes the last element
          setCompositedImageSrcs(imageSrcArray);
        } else {
          // Handle the case where no jsonResponse.results are returned
          setImageSrc(""); // Reset or set to a default image or placeholder
          setCompositedImageSrcs([]); // Ensure the array state is empty
        }

        setCompositedImageSrcs(imageSrcArray);
      } else {
        throw new Error("Failed to fetch results");
      }
    } catch (error) {
      console.error("Error during form submission:", error);
    }
    setLoading(false); //  
  };

  //    -------------------
  const handleImageClick = (index) => {
    const updatedCheckedImages = [...checkedImages];
    updatedCheckedImages[index] = !updatedCheckedImages[index];
    setCheckedImages(updatedCheckedImages);

    // Update clicked indices with index adjusted by adding 6
    if (updatedCheckedImages[index]) {
      setClickedImageIndices([...clickedImageIndices, index]); // Add (index + 6) to the array if the image is checked
    } else {
      setClickedImageIndices(
        clickedImageIndices.filter((idx) => idx !== index)
      ); // Remove (index + 6) from the array if the image is unchecked
    }

    const updatedSelectedImageSrcs = updatedCheckedImages
      .map((checked, idx) => {
        return checked ? imageNames[idx] : null;
      })
      .filter((src) => src !== null);

    setSelectedImageSrcs(updatedSelectedImageSrcs);
  };
  //----------------------------------------------------------------

  const handleAIButtonClick = () => {
    setShowAIButton(false);
    setShowGenerator(true); // Show the generator with the animation
  };

  const getButtonStyle = (index) => {
    if (index === step) {
      return {
        color: "white",
      };
    }
    return {};
  };

  const handleStepClick = (index) => {
    const updatedActiveSteps = [...activeSteps];
    updatedActiveSteps[index] = true;
    setActiveSteps(updatedActiveSteps);
    setStep(index);
  };

  const handleOptionClick = (option, index) => {
    const newSelectedOptions = [...selectedOptions];
    newSelectedOptions[step] = option;
    setSelectedOptions(newSelectedOptions);
    setOtherInput("");
  };

  const handleInputClick = () => {
    setSelectedOptions(
      selectedOptions.map((item, idx) => (idx === step ? null : item))
    );
  };

  const handleInputChange = (event) => {
    setOtherInput(event.target.value);
    const updatedOptions = [...selectedOptions];
    updatedOptions[step] = event.target.value;
    setSelectedOptions(updatedOptions);
  };

  const handleNextButtonClick = (event) => {
    event.preventDefault();
    let newStep = step + 1;
    if (newStep < options.length) {
      setStep(newStep);
      const updatedActiveSteps = [...activeSteps];
      updatedActiveSteps[newStep] = true;
      setActiveSteps(updatedActiveSteps);
      setOtherInput("");
    }
    if (newStep === options.length - 1) {
      SetNextBtn(" ");
    }
    if (newStep === options.length) {
      setIsGenerated(true);
      setShowOptions(false);
      handleFormSubmit();
      setShowGenerator(false); //  
    }

    const timer = setTimeout(() => {
      setAnimate(true);
    }, 4000); // 4    
  };

  return (
    <>
      <div className={`About ${scrollAnimation ? "slide-up" : "hidden"}`}>
        <h3>      .</h3>
        <p>
          .    .  Stable Diffusion
          IDM-Vton       .
                   
            .      !
        </p>
        <img src={process.env.PUBLIC_URL + "/images/dress8.webp"} alt="Dress" />
      </div>

      <div className="Main-container">
        <div className="img">
          <div className="content">
            <br></br>
            <h1>Wedding Dress Virtual Fitting</h1>
            <p style={{ marginTop: "30px" }}>
              We provide a virtual fitting service utilizing Stable-Diffusion
              and IDM-Vton
              <br></br>
              Discover and try on the dress that perfectly matches your style
            </p>

            <button className="getStart" onClick={scrollTo1800px}>
              Get Start
            </button>
          </div>
          <div className="img-cover"></div>
        </div>

        <div id="recommendation-section" className="demonstration-container">
          <h3>
            Create <span style={{ color: "orange" }}>wedding dress</span> like
            these
          </h3>
          <br></br> <br></br>
          <ControlCarousel />
          <br></br>
          <p>You can create and fit a dress just like the example above</p>
        </div>

        <div
          className="generating-options"
          style={{ background: backgroundColor, height: "100vh" }}
        >
          <div
            className="light-background"
            style={{ zIndex: lightBackgroundZIndex }}
          >
            <div
              className={`dark-background ${isActive ? "hidden" : ""}`}
              style={{ zIndex: darkBackgroundZIndex }}
            >
              {loading ? (
                <div className="spinner-container">
                  <HashLoader color="#54f3b2" size={150} />
                </div>
              ) : showAIButton ? (
                <div
                  className="fontbox"
                  style={{ width: "100%", color: "white" }}
                >
                  <img
                    src="/images/logo5.png"
                    style={{ position: "static", width: "200px" }}
                  />
                  <h1>
                    What's Your <br /> BEST DRESS?
                  </h1>
                  <br />

                  <img
                    src={process.env.PUBLIC_URL + "/images/dress9.png"}
                    alt="Dress"
                  />
                  <br />
                  <button className="button-ai" onClick={handleAIButtonClick}>
                     
                  </button>
                </div>
              ) : (
                showGenerator && (
                  <div className="generating-container">
                    <form>
                      <div className="generating-dress">
                        <h1>AI  </h1>
                        <p>  : 2</p>
                        <div className="pagination">
                          {Array.from({ length: options.length }).map(
                            (_, index) => (
                              <button
                                type="button" //     type button 
                                key={index}
                                style={getButtonStyle(index)}
                                className={activeSteps[index] ? "active" : ""}
                                onClick={() => handleStepClick(index)}
                              ></button>
                            )
                          )}
                        </div>
                        <br />
                        <p className="generating-title">
                          {options[step].question}
                        </p>

                        <div className="options-case">
                          {options[step].options.map((option, index) => (
                            <button
                              type="button" //      
                              key={index}
                              className={`options ${
                                selectedOptions[step] === option
                                  ? "selected"
                                  : ""
                              }`}
                              onClick={() => handleOptionClick(option, index)}
                            >
                              {option}
                            </button>
                          ))}
                        </div>

                        <br />
                        <input
                          type="text"
                          placeholder="Other"
                          className="custom-input"
                          onClick={handleInputClick}
                          onChange={handleInputChange}
                          value={otherInput}
                        />
                        <br />
                        <br />
                        <button
                          type="submit" //   
                          className="options next-button"
                          onClick={handleNextButtonClick}
                        >
                          {nextBtn}
                        </button>
                      </div>
                    </form>
                  </div>
                )
              )}
            </div>

            {/*     */}

            <div>
              <div
                className="spinner-container"
                style={{ visibility: loading ? "visible" : "hidden" }}
              >
                <HashLoader color="#54f3b2" size={150} />
              </div>
              <div className="content">
                {loading ? (
                  <div className="spinner-container">
                    <HashLoader color="#54f3b2" size={150} />
                  </div>
                ) : showResults ? (
                  //      
                  <div
                    style={{
                      width: "100%",
                      height: "100%",
                      textAlign: "center",
                    }}
                  >
                    <h1 className="logo5">
                      {" "}
                      <img src="/images/logo1.png" />
                    </h1>
                    <div
                      className="results-container"
                      style={{ display: "flex" }}
                    >
                      <div style={{ flex: 2 }}>
                        <h2 style={{ fontWeight: "bold", marginTop: "20px" }}>
                            
                        </h2>
                        <br></br>
                        <h5 style={{ fontWeight: "bold", marginTop: "50px" }}>
                             
                        </h5>
                        <img
                          src={imageSrc}
                          alt="Full body upload"
                          style={{
                            width: "180px",
                            height: "250px",
                            maxWidth: "500px",
                          }}
                        />
                      </div>

                      <div style={{ flex: 8 }}>
                        <div
                          className="original-images"
                          style={{
                            display: "flex",
                            flexWrap: "wrap",
                            alignItems: "center",
                            justifyContent: "center",
                          }}
                        >
                          {selectedImageSrcs.map((src, index) => (
                            <img
                              key={index}
                              src={src}
                              alt={`Selected Dress ${index + 1}`}
                              style={{
                                width: `14%`,
                                height: "250px",
                                margin: "1%",
                              }}
                            />
                          ))}
                          {/*        */}
                          {Array.from({
                            length: 6 - selectedImageSrcs.length,
                          }).map((_, index) => (
                            <div
                              key={`placeholder-${index}`}
                              style={{
                                width: `14%`,
                                height: "250px",
                                margin: "1%",
                                backgroundColor: "#ccc", //  
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                color: "white",
                                fontSize: "20px",
                              }}
                            >
                               
                            </div>
                          ))}
                        </div>
                        {/*    */}
                        <div
                          style={{
                            width: "100%",
                            textAlign: "center",
                            margin: "10px 0",
                          }}
                        >
                          <span style={{ fontSize: "24px" }}></span>
                        </div>
                        <div
                          className="composite-images"
                          style={{
                            display: "flex",
                            flexWrap: "wrap",
                            alignItems: "center",
                            justifyContent: "center",
                          }}
                        >
                          {compositedImageSrcs.map((src, index) => (
                            <img
                              key={index}
                              src={src} // Use dynamic src from your state or props
                              alt={`Composite Dress ${index + 1}`}
                              className="fade-in-top"
                              style={{
                                width: `14%`,
                                height: "250px",
                                margin: "1%",
                              }}
                            />
                          ))}
                          {/* Placeholder for empty spaces */}
                          {Array.from({
                            length: 6 - compositedImageSrcs.length,
                          }).map((_, index) => (
                            <div
                              key={`placeholder-composite-${index}`}
                              style={{
                                width: `14%`,
                                height: "250px",
                                margin: "1%",
                                backgroundColor: "#ccc",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                                color: "white",
                                fontSize: "20px",
                              }}
                            >
                               
                            </div>
                          ))}
                        </div>
                      </div>
                    </div>
                  </div>
                ) : (
                  //   
                  <>
                    <form onSubmit={handleVirtualFittingStart}>
                      <div className="generating-results">
                        <h1 style={{ zIndex: lightBackgroundZIndex }}>
                          <img src="/images/logo1.png" />
                        </h1>
                        <br></br>
                      </div>
                      <br></br>

                      <div className="dress-gallery">
                        {imageNames.map((src, index) => (
                          <div
                            key={index}
                            className="img-box"
                            onClick={() => handleImageClick(index)}
                          >
                            <img
                              src={src}
                              alt={`Dress ${index + 1}`}
                              className={animate ? "slide-down" : ""}
                              style={{
                                border: checkedImages[index]
                                  ? "2px solid red"
                                  : "none",
                                cursor: "pointer",
                              }}
                            />
                          </div>
                        ))}
                      </div>

                      <div className="btn-box">
                        <input
                          type="file"
                          accept="image/*"
                          style={{ display: "none" }}
                          onChange={handleImageChange}
                          ref={fileInput}
                        />
                        <button
                          type="button" //     type button 
                          className="body-btn"
                          style={{
                            zIndex: lightBackgroundZIndex,
                            background: buttonBackground, //    
                            transition: "background 2s ease", //     
                          }}
                          onClick={() =>
                            fileInput.current && fileInput.current.click()
                          }
                        >
                          {renderTextWithBreaks(
                            buttonText,
                            "   "
                          )}
                        </button>
                        <button
                          type="submit" //    
                          className="body-btn"
                          style={{ zIndex: lightBackgroundZIndex }}
                          onClick={handleVirtualFittingStart}
                        >
                          {renderTextWithBreaks(buttonText2, "  ")}
                        </button>
                      </div>
                    </form>
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Generator;
