import Carousel from "react-bootstrap/Carousel";

function ControlCarousel() {
  return (
    <Carousel id="carousel-container" >
      <Carousel.Item>
        <div className="slidercontents">
        
          <img src={process.env.PUBLIC_URL + '/images/dress1.jpg'} alt="Dress" />

        </div>
      </Carousel.Item>
      <Carousel.Item>
        <div className="slidercontents">
        <img src={process.env.PUBLIC_URL + '/images/dress4.jpg'} alt="Dress" />

        </div>
      </Carousel.Item>

      <Carousel.Item>
        <div className="slidercontents">
        <img src={process.env.PUBLIC_URL + '/images/dress6.jpg'} alt="Dress" />

        </div>
      </Carousel.Item>
    </Carousel>
  );
}


export default ControlCarousel;